    createGenerators(generatorData) {
        generatorData.forEach(data => {
            const { id, genChainID, name, description, level, maxLevel = new Decimal(Infinity), costType, costBase, costGrowthRate, prodType, prodBase, prodGrowthRate, active } = data;
            const generator = new Generator(this.eventManager, id, genChainID, name, description, level, maxLevel, costType, costBase, costGrowthRate, prodType, prodBase, prodGrowthRate, active);
            this.generators.push(generator);
            this.gameManager.gameContent.generators.push(generator);
            this.gameManager.gameContent.idToObjectMap.set(id, generator);

            //push generators to generatorchains
            const genChain = this.generatorChains.find(genChain => genChain.id === generator.genChainID);
            if (genChain) {
                genChain.generators.push(generator);
            }
        });
    }

