class GameManager {
    constructor(eventManager) {
        this.eventManager = eventManager;
        this.gameContent = new GameContent(eventManager);

        this.multiplierString = "1";

        this.eventManager.addListener('queryPlayerResource', (data, respond) => {
            const result = this.queryPlayerResource(data);
            if (respond) {
                respond(result);
            }
        });

        this.eventManager.addListener('handlePurchase', (data) => {
            this.handlePurchase(data.id);
        });

        this.eventManager.addListener('updateFeatureValues', (data) => {
            this.updateFeatureValues(data.target, data.isNewLvl);
        });

        this.eventManager.addListener('updateNewMultiplierValues', (data = this.multiplierString) => {
            this.updateNewMultiplierValues(data.multiplierString);
        });
    }

    onMultiplierChange(newMultiplierString) {
        this.multiplierString = newMultiplierString;
        this.updateNewMultiplierValues(newMultiplierString);
    }

    updateNewMultiplierValues(newMultiplierString = this.multiplierString) {
        let multipliableFeatures = this.gameContent.trainings.concat(this.gameContent.upgrades)
            .concat(this.gameContent.skillTree.skills)
            .concat(this.gameContent.generators)
            .concat(this.gameContent.essenceUpgrades)
            .concat(this.gameContent.fighters)
            .filter(feature => !feature.level.equals(feature.maxLevel));

        //nextMilestone
        if (newMultiplierString === "nextMilestone") {
            for (const feature of multipliableFeatures) {
                if (feature.featureType === "training") {
                    feature.updateValuesMilestone();
                }
                else {
                    feature.updateValuesDigit(new Decimal(1));
                }
            }
        }

        //max
        else if (newMultiplierString === "max") {
            for (const feature of multipliableFeatures) {
                const allResources = this.queryPlayerResource("all");
                let matchingResource = allResources.find(resource => resource.type === feature.costType);
                feature.updateValuesMax(matchingResource.value);
            }
        }

        //1,5,10,100
        else {
            let n = new Decimal(newMultiplierString);
            for (const feature of multipliableFeatures) {
                feature.updateValuesDigit(n);
            }
        }
    }

    handlePurchase(featureID) {
        const feature = this.findObjectById(featureID);

        this.deductFeatureCost(feature);
        this.updateFeatureValues(feature,true);

        feature.levelUp("manual");

        this.updateNewMultiplierValues(this.multiplierString);

        this.eventManager.dispatchEvent('check-unlocks');
    }

    updateFeatureValues(feature, isNewLvl) {
        //handle if an active feature's multiplier is being updated but not its level
        if (feature.active && !isNewLvl) {
            feature.prodPrevious = feature.prodCurrent;
            feature.prodCurrent = feature.calculateProdN(feature.level, 0);
            feature.costNext = feature.calculateCostN(feature.manualLevel.plus(1), 0);
        }

        //dont update values if inactive || (level 0 & not being levelled up)
        else if (!feature.active || (feature.level.eq(0) && !isNewLvl)) {
            return;
        }
        //handle features that are being levelled manually
        else {
            feature.prodPrevious = feature.prodCurrent;
            feature.prodCurrent = feature.prodCurrent.plus(feature.prodNext);
        }


        let dif = feature.prodCurrent.minus(feature.prodPrevious);

        if (feature.prodType === "powerIncome") {
            this.gameContent.player.powerIncome = this.gameContent.player.powerIncome.plus(dif);
        }
        else if (feature.prodType === "spiritIncome") {
            this.gameContent.player.spiritIncome = this.gameContent.player.spiritIncome.plus(dif);
        }

        if (feature.featureType === "essenceUpgrade") {
            switch (feature.prodType) {
                case 'skillpoint':
                    this.gameContent.player.skillpoint = this.gameContent.player.skillpoint.plus(feature.prodCurrent);

                    this.gameContent.player.baseSkillpoint = this.gameContent.player.baseSkillpoint.plus(feature.prodCurrent);
                    break;
                case 'baseFeatureLevel':
                    feature.target.baseLevel = feature.target.baseLevel.plus(feature.prodCurrent);
                    feature.target.updateValuesDigit(feature.prodCurrent);
                    feature.target.levelUp("auto", feature.prodCurrent);
                    this.updateFeatureValues(feature.target, true);
                    break;
                case 'fighterSkillpoint':
                    //handle retroactive defeated fighter skillpoint upgrades
                    //also updated in stage.handlefight part of this solution
                    for (const fighter of this.gameContent.fighters) {
                        if (fighter.active && fighter.isDefeated) {
                            fighter.prodCurrent = fighter.prodPrevious;
                            fighter.prodNext = fighter.prodCurrent.times(fighter.prodMult);
                            let skillpointstoadd = fighter.prodNext;
                            this.gameContent.player.skillpoint = this.gameContent.player.skillpoint.plus(skillpointstoadd);
                        }
                    }
                    break;
            }
            return;
        }
    }

    deductFeatureCost(feature) {
        let cost = feature.costNext;
        switch (feature.costType) {
            case 'power':
                this.gameContent.player.power = this.gameContent.player.power.minus(cost);
                break;
            case 'spirit':
                this.gameContent.player.spirit = this.gameContent.player.spirit.minus(cost);
                break;
            case 'skillpoint':
                this.gameContent.player.skillpoint = this.gameContent.player.skillpoint.minus(cost);
                break;
            case 'essence':
                this.gameContent.player.essence = this.gameContent.player.essence.minus(cost);
                break;
            case 'powerLevel':
                //do not deduct anything
                break;
            default:
                console.error('Invalid subtractcost');
        }
    }


    calculatePlayerIncome(deltaTime) {
        for (const chain of this.gameContent.generatorChains) {
            if (chain.active) {
                chain.calculateChain(this);
            }
        }

        // multiply each income by deltaTime
        this.gameContent.player.power = this.gameContent.player.power.plus(this.gameContent.player.powerIncome.times(deltaTime));
        this.gameContent.player.spirit = this.gameContent.player.spirit.plus(this.gameContent.player.spiritIncome.times(deltaTime));

        this.gameContent.player.powerLevel = this.gameContent.player.powerLevel.plus(this.gameContent.player.powerIncome.times(deltaTime));
        this.gameContent.player.spiritLevel = this.gameContent.player.spiritLevel.plus(this.gameContent.player.spiritIncome.times(deltaTime));

        this.gameContent.player.lifetimePowerEarned = this.gameContent.player.lifetimePowerEarned.plus(this.gameContent.player.powerIncome.times(deltaTime));
        this.gameContent.player.lifetimeSpiritEarned = this.gameContent.player.lifetimeSpiritEarned.plus(this.gameContent.player.spiritIncome.times(deltaTime));
    }



    queryPlayerResource(type) {
        switch (type) {
            case 'power':
                return this.gameContent.player.power;
            case 'spirit':
                return this.gameContent.player.spirit;
            case 'essence':
                return this.gameContent.player.essence;
            case 'skillpoint':
                return this.gameContent.player.skillpoint;
            case 'crystal':
                return this.gameContent.player.crystal;
            case 'powerLevel':
                return this.gameContent.player.powerLevel;
            case 'spiritLevel':
                return this.gameContent.player.spiritLevel;
            case 'all':
                return [
                    { 'type': 'power', 'value': this.gameContent.player.power },
                    { 'type': 'spirit', 'value': this.gameContent.player.spirit },
                    { 'type': 'essence', 'value': this.gameContent.player.essence },
                    { 'type': 'skillpoint', 'value': this.gameContent.player.skillpoint },
                    { 'type': 'crystal', 'value': this.gameContent.player.crystal },
                    { 'type': 'powerLevel', 'value': this.gameContent.player.powerLevel },
                    { 'type': 'spiritLevel', 'value': this.gameContent.player.spiritLevel }
                ];
            default:
                console.error('Invalid stat:', type);
        }
    }

    findObjectById(id) {
        let object = this.gameContent.idToObjectMap.get(id);
        return object;
    }

    findTrainingById(trainingId) {
        // Search for the training object in the powerRealms array
        for (const realm of this.gameContent.powerRealms) {
            const training = realm.trainings.find(t => t.id === trainingId);
            if (training) return training;
        }

        // Search for the training object in the spiritRealms array
        for (const realm of this.gameContent.spiritRealms) {
            const training = realm.trainings.find(t => t.id === trainingId);
            if (training) return training;
        }

        // If the training object is not found in either array, return undefined
        return undefined;
    }

}

class EventManager {
    constructor() {
        this.listeners = {};
    }

    // Register an event listener
    addListener(eventType, callback) {
        if (!this.listeners[eventType]) {
            this.listeners[eventType] = [];
        }
        this.listeners[eventType].push(callback);
    }

    // Remove an event listener
    removeListener(eventType, callback) {
        if (this.listeners[eventType]) {
            const index = this.listeners[eventType].indexOf(callback);
            if (index > -1) {
                this.listeners[eventType].splice(index, 1);
            }
        }
    }


    // Dispatch an event to all registered listeners
    dispatchEvent(eventType, eventData, handleResult) {
        if (this.listeners[eventType]) {
            this.listeners[eventType].forEach(callback => {
                if (typeof handleResult === 'function') {
                    callback(eventData, handleResult);
                } else {
                    callback(eventData);
                }
            });
        }
    }

    dispatchQuery(eventType, eventData) {
        return new Promise((resolve) => {
            this.dispatchEvent(eventType, eventData, resolve);
        });
    }
}

