class Achievement extends GameFeature {
    constructor(eventManager, id, name, description, conditionType, conditionValue, targetType, targetID) {
        super(eventManager, id, name, description, new Decimal(0), new Decimal(1), null, null, null, null, null, null, false);

        this.conditionValue = conditionValue;
        this.conditionType = conditionType;

        this.isCliamable = false;
        this.isClaimed = false;

        this.targetType = targetType;
        this.targetID = targetID;
        this.target = null;
    }

    claim(gameManager) {
        this.level = this.level.plus(1);
        this.isClaimed = true;
        this.active = true;
        for (const observer of this.observers) {
            observer.active = true;
        }
        this.updateObservers(gameManager);
    }
}

