class World {
    constructor(eventManager, id, name, active) {
        this.eventManager = eventManager;
        this.id = id;
        this.name = name;

        this.regions = [];

        this.active = active;
        this.isCompleted = false;
    }

    setCompleted() {
        this.isCompleted = true;
    }
    setActive() {
        this.active = true;
        this.regions[0].setActive();
    }

    regionCompleted() {
        const allRegionsCompleted = this.regions.every(region => region.isCompleted);

        if (allRegionsCompleted) {
            this.setCompleted();
        }
        else {
            const nextRegion = this.regions.find(region => !region.active);
            if (nextRegion) {
                nextRegion.setActive();
            }
        }
    }

}

class Region {
    constructor(eventManager,id,worldID,name, shardType, active) {
        this.eventManager = eventManager;
        this.id = id;
        this.worldID = worldID;
        this.world = null;
        this.name = name;

        this.shardType = shardType;

        this.tournament = null;
        this.workers = [];

        this.active = active;
        this.isCompleted = false;
    }

    setCompleted() {
        this.isCompleted = true;
        this.world.regionCompleted();
    }

    setActive() {
        this.active = true;
        this.tournament.setActive();
    }
}

class Tournament {
    constructor(eventManager, id, regionID, name, active) {
        this.eventManager = eventManager;
        this.id = id;
        this.regionID = regionID;
        this.region = null;

        this.name = name;
        this.active = active;
        this.isCompleted = false;
        this.fighters = [];
    }

    handleFight(fighterId) {
        let fighter = this.fighters.find(fighter => fighter.id === fighterId);
        if (!fighter) {
            console.error("fighter not found. no fight processed");
            return;
        }

        fighter.setDefeated();

        this.eventManager.dispatchEvent('addPlayerResource', { rewardType: fighter.prodType, rewardValue: fighter.prodNext });

        this.checkAllFightersDefeated();

        fighter.prodPrevious = fighter.prodCurrent = fighter.prodNext;

        this.eventManager.dispatchEvent('check-unlocks');
    }

    setActive() {
        this.active = true;
        this.fighters.forEach(fighter => {
            fighter.active = true;
        });
    }

    checkAllFightersDefeated() {
        const allFightersDefeated = this.fighters.every(fighter => fighter.isDefeated);
        if (allFightersDefeated) {
            this.isCompleted = true;
            this.region.setCompleted();
        }
    }
}

